:root {
  --theme-color: #ec8951;
  --bs-table-color: #222;
}

/**=====================
    63.  Document CSS Start
==========================**/
.pl-40 {
  padding-left: 40px;
}

.mt-40 {
  margin-top: 40px;
}

.mt-50 {
  margin-top: 50px;
}

.warning-block {
  background-color: rgba(255, 166, 64, 0.5);
  padding: 20px;
  border-left: 5px solid #ff9f40;
}

.dark-block {
  background-color: rgba(224, 217, 209, 0.3);
  padding: 20px;
  border-left: 5px solid #2f3c4e;
}

ul.list-style {
  margin-bottom: 2rem;
}
ul.list-style li {
  margin-bottom: 0.5rem;
  list-style-type: circle;
}
ul.list-style li:last-child {
  margin-bottom: 0;
}

.card {
  margin-bottom: calc(20px + 10 * (100vw - 320px) / 1600);
  transition: all 0.3s ease;
  letter-spacing: 0.5px;
  border-radius: 5px;
  box-shadow: 0 0 8px #e1e1e1;
  border: 0;
  background-color: unset;
}
.card .card-header {
  padding: calc(10px + 4 * (100vw - 320px) / 1600) calc(10px + 10 * (100vw - 320px) / 1600);
  border-bottom: 0;
}
.card .card-header h5 {
  font-weight: 400;
  text-transform: uppercase;
  font-size: 17px;
}
.card .card-body {
  border: none;
  padding: 0;
}

.border-img {
  border: 1px solid #eee;
}

.page-footer {
  margin: 0;
  display: block;
  padding: 0;
}
.page-footer .page-changes-summary {
  border-top: 2px solid #e8ebf2;
  margin-top: 25px;
  margin-bottom: 25px;
  align-items: center;
  padding-top: 25px;
}
.page-footer .page-changes-summary .contributors {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.page-footer .page-changes-summary .contributors .avtar-list {
  display: flex;
  align-items: center;
}
.page-footer .page-changes-summary .contributors .avtar-list .page-edit-date i {
  margin-right: 5px;
  color: #898989;
  font-size: 20px;
}
.page-footer .page-changes-summary .contributors .page-rating {
  display: flex;
}
.page-footer .page-changes-summary .contributors .page-rating .page-rating-title {
  margin-right: 15px;
}
.page-footer .page-changes-summary .contributors .page-rating .page-rating-icons span {
  color: rgb(116, 129, 141);
  width: 1.4em;
  cursor: pointer;
  height: 1.4em;
  display: inline-flex;
  font-size: 18px;
  align-items: center;
  justify-content: center;
  background-color: rgb(230, 236, 241);
  margin: 0px 8px;
  padding: 0px;
  border-radius: 50%;
  transition: background-color 250ms ease 0s, color 250ms ease 0s, tranform 250ms ease 0s;
}
.page-footer .page-changes-summary .contributors .page-rating .page-rating-icons span:hover {
  background-color: #a5a5a5;
  color: white;
  transition: all 0.3s ease;
}
.page-footer .page-changes-summary .contributors .page-rating .page-rating-icons span:nth-child(2):hover {
  background-color: #ffbc58;
}
.page-footer .page-changes-summary .contributors .page-rating .page-rating-icons span:nth-child(3):hover {
  background-color: #81ba00;
}
.page-footer .avtar-list img {
  width: 30px;
}

.btn-group-element {
  margin-bottom: -15px;
}
.btn-group-element > * {
  margin-bottom: 15px;
}

.m-width {
  max-width: 1550px;
}

.main-title {
  color: #333;
  letter-spacing: 1px;
  margin-bottom: 20px;
  font-weight: 700;
}

.border-shape {
  display: block;
  left: 0;
  margin: 15px auto;
  position: relative;
  right: 0;
  text-align: center;
  top: 0;
  width: 80px;
  height: 4px;
}

.features {
  background-color: white;
  border-radius: 15px;
  border: 1px solid #dddddd;
  justify-content: center;
}
.features .f-block {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  text-align: center;
  border-right: 1px solid #dddddd;
  border-bottom: 1px solid #dddddd;
}
.features .f-block:nth-child(6n) {
  border-right: none;
}
.features .f-block:nth-child(19), .features .f-block:last-child {
  border-bottom: none;
}
.features .f-block:hover img {
  box-shadow: 0 0 35px white;
  transition: all 0.3s ease-in-out;
}
.features .f-block:focus img {
  background-color: yellow;
}
.features .f-block:last-child {
  border-right: none;
}
.features .f-block img {
  border-radius: 100%;
  width: 80px;
  overflow: hidden;
  transition: all 0.9s ease-out;
}
.features .f-block h6 {
  transition: all 0.9s ease-out;
}
.features .f-block h6 {
  font-weight: 700;
  color: #777;
  text-align: center;
  margin-top: 10px;
  font-size: 14px;
}

.star-sec {
  display: flex;
  align-items: center;
}
.star-sec .fa-star {
  color: #f2b827;
  margin-right: 5px;
}

.right-star {
