  align-items: center;
}
.right-star p {
  margin-bottom: 0;
}
.right-star p span {
  color: #0084b4;
}

.review-sec .review-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.review-sec .card-body p {
  color: #666666;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.8;
  padding: 0;
}
.review-sec .card-body .media {
  margin-bottom: 20px;
}
.review-sec .btn {
  font-weight: 600;
}

.brand-sec {
  cursor: pointer;
  display: flex;
  align-items: center;
  text-decoration: none;
}
.brand-sec h3 {
  font-size: 24px;
  font-weight: 500;
  line-height: 1.5;
  overflow-wrap: break-word;
  color: #242a31;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 10px;
}

.quick-links h5 {
  padding: 15px 20px;
  margin-bottom: 10px;
  background-color: rgba(0, 0, 0, 0.05);
  text-transform: uppercase;
  font-weight: 700;
  font-size: 15px;
  color: #2a3142;
}
.quick-links li {
  padding-bottom: 5px;
}
.quick-links li a {
