  color: #898989;
  font-weight: 600;
}
.quick-links li a:hover {
  letter-spacing: 1.5px;
  color: var(--theme-color);
}

.main-table-sec h5 {
  color: #242a31;
  margin: 0;
  display: flex;
  position: relative;
  margin-bottom: 24px;
}
.main-table-sec .table-wrapper {
  margin: 0;
  display: block;
  padding: 0;
  overflow: visible;
  position: relative;
}
.main-table-sec .table-wrapper .table-scroll {
  margin: 0;
  display: block;
  padding: 0;
  overflow: auto;
  position: relative;
}
.main-table-sec .table-wrapper .table-scroll table {
  width: 100%;
  position: relative;
  border-collapse: collapse;
  page-break-inside: auto;
