  font-weight: 600;
  color: #2a3142;
}
.main-table-sec .table-wrapper .table-scroll table tr td {
  width: auto;
  padding: 8px;
  position: relative;
  align-self: start;
  border-top: none;
  align-items: start;
  border-right: none;
  border-bottom: none;
  -webkit-box-align: start;
  text-align: left;
}
.main-table-sec .table-wrapper .table-scroll table tr td span {
  font-size: 100%;
  font-weight: 500;
  line-height: 1.625;
  margin-right: 5px;
}

.link-primary {
  color: var(--theme-color);
  cursor: pointer;
  text-decoration: none;
  opacity: 0.7;
}
.link-primary:hover {
  color: var(--theme-color);
  opacity: 1;
}

.mode {
